.thank-wrapper {
  z-index: 7;
  background: #e03f5e;
  border-radius: 20px;
  width: 335px;
  height: 426px;
  margin: 0 auto;
  padding-top: 40px;
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: fixed;
  top: 25%;
  left: 30%;
  overflow: scroll;
}

.is-hidden-thankyou {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.thank-you-button {
  color: #fff;
  background: #e03f5e 0 0 / contain;
  border: none;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  padding: 0;
  transition: transform .3s cubic-bezier(.4, 0, .2, 1), color .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
  transform: rotate(-180deg);
}

.thank-you-button:hover {
  color: #f47f4c;
  transform: rotate(0);
}

@media only screen and (min-width: 768px) {
  .thank-you-button {
    width: 28px;
    height: 28px;
  }

  .thank-img {
    max-width: 278px;
    max-height: 239px;
    margin-bottom: 28px;
  }

  .thank-wrapper {
    width: 600px;
    height: 545px;
  }

  .thank-you-text {
    width: 391px;
    margin: 0 auto;
  }
}

.thank-close-svg {
  text-align: center;
  stroke: currentColor;
  background-size: contain;
  justify-content: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.thank-img {
  text-align: center;
  width: 278px;
  height: 239px;
  margin-bottom: 20px;
  margin-left: 150px;
  margin-right: auto;
  display: block;
}

.thank-title {
  text-align: center;
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: #fff;
  text-shadow: 0 4px 70px #0000001a;
  margin-bottom: 14px;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px;
}

.thank-you-text {
  text-align: center;
  color: #fff;
  text-shadow: 0 4px 70px #0000001a;
  letter-spacing: -.04em;
  width: 391px;
  margin: 0 auto;
  font-size: 14px;
